import swiperImage from "@/components/index/swiper-image.vue";
import indexNav    from "@/components/index/index-nav.vue";
import threeAdv    from "@/components/index/three-adv.vue";
import card        from "@/components/common/card.vue";
import commonList  from "@/components/common/common-list.vue";

export default {
	components: {
		// mediaList,
		swiperImage,
		indexNav,
		threeAdv,
		card,
		commonList
	},
	data() {
		return {
			scrollinto: 'tab0',
			windowHeight: 0,
			tabIndex: 0,
			tabBars: [],
			newsitems: [{}]
		}
	},
	// 页面载入的时候加载数据
	onLoad: function() {
		// 获取可视区域高度(原生头和底部之外的地方)
		uni.getSystemInfo({
			success:(e) => { 
				let navbarH = 0

					navbarH = uni.upx2px(90)

				// 这里得到的height单位是px
				this.windowHeight = e.windowHeight - uni.upx2px(81) -navbarH;
			}
		})
		
		// 初始化事件
		this.__init()
		
	},
	methods: {
		// 初始化事件
		__init(){
			// 获取顶部选项卡
			this.$H.get('/index_category/data').then((res)=>{
				console.log('请求到的首屏数据：',res);
				this.tabBars = res.category
				// 根据顶部选项卡生成页面
				let arr = []
				for (var i = 0; i < this.tabBars.length; i++) {
					// 初始化每个页面的obj
					let obj = {
						list:[],
						// 1.上拉加载更多 2.加载中... 3.没有更多了
						loadingText: "上拉加载更多",
						// 是否首次加载
						firstLoad: true
					}
					// 获取首屏数据
					if (i === 0) {
						obj.list = res.data
						obj.firstLoad = false
					}
					
					arr.push(obj)
				}
				this.newsitems = arr
			})
		},
		// 切换选项卡
		changeTab(index) {
			if(this.tabIndex === index) return;
			this.tabIndex = index;
			this.scrollinto = 'tab' + index;
			if(this.newsitems[index].firstLoad) this.addData();
		},
		
		// 监听滑动列表
		onChangeTab(e) {
			this.changeTab(e.detail.current);
		},
		
		// 加载数据
		async addData(callback = false) {
			// 拿到当前的索引
			let index = this.tabIndex
			// 拿到当前对象
			let obj = this.newsitems[index]
			// 获取当前分类id
			let id = this.tabBars[index].id
			// 获取当前分页数
			let page = Math.ceil(obj.list.length/5) + 1
			
			// 请求数据
			let data = await this.$H.get(`/index_category/${id}/data/${page}`)
			console.log('data:',data)
			// 标记该页面已加载过 -> 非首次加载
			obj.firstLoad = false
			// 回调
			if(typeof callback === 'function') callback()
			// 请求不到数据 就终止
			if(!data) return obj.loadingText = "没有更多了"
			// 追加渲染数据
			obj.list = [...obj.list, ...data]
			// 根据获取到的数据条目 -> 没有更多了
			obj.loadingText = data.length < 5 ? "没有更多了" : "上拉加载更多"
		},
		
		// 上拉加载更多
		loadMore(index){
			// 拿到当前对象
			let obj = this.newsitems[index]
			// 是否处于可加载状态
			if(obj.loadingText !== "上拉加载更多") return
			
			// 先将状态设置为 "加载中..."				
			obj.loadingText = "加载中..."
			
			// 加载数据
			this.addData(()=>{
				uni.showToast({
					title: '加载成功',
					icon: 'none'
				});
			})
			
			// 数据加载完毕之后更改状态 "上拉加载更多"/"没有更多了"
			// todo...（这部分在addData里面完成了）

		},
		
		
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select('#' + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async tapTab(index) { //点击tab-bar
			if (this.tabIndex === index) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = index;
			}
		},
	}
}
